/**
 * audio 扩展包
 * @tutorial https://github.com/jin-yufeng/Parser
 * @author JinYufeng
 */
export default {
	data() {
		return {
			error: false,
			playing: false,
			time: '00:00',
			value: 0
		}
	},
	props: {
		author: String,
		autoplay: null,
		controls: null,
		loop: null,
		name: String,
		poster: String,
		src: String
	},
	watch: {
		src(src) {
			this.setSrc(src);
		}
	},
	beforeDestroy() {
		this.pause();
	},





	mounted() {
		this._ctx = uni.createInnerAudioContext();
		this._ctx.onError((err) => {
			this.error = true;
			err.context = this;
			this.$emit('error', err);
		})





			this._ctx.onTimeUpdate(() => {

				var time = this._ctx.currentTime,
					min = parseInt(time / 60),
					sec = Math.ceil(time % 60);
				this.time = (min > 9 ? min : '0' + min) + ':' + (sec > 9 ? sec : '0' + sec);
				if (!this.lastTime) this.value = time / this._ctx.duration * 100; // 不在拖动状态下

			})




		this._ctx.onEnded(() => {
			this.playing = false;
		});
		this.setSrc(this.src);
	},
	onPageShow() {
		if (this.playing && this._ctx.paused)
			this._ctx.play();
	},
	methods: {
		toJSON() {},
		// 设置源
		setSrc(src) {
			this._ctx.autoplay = this.autoplay;
			this._ctx.loop = this.loop;
			this._ctx.src = src;
		},
		// 播放
		play() {
			this._ctx.play();
			this.playing = true;
			this.$emit('play');
		},
		// 暂停
		pause() {
			this._ctx.pause();
			this.playing = false;
			this.$emit('pause');
		},
		// 移动进度条
		seek(sec) {
			this._ctx.seek(sec);
		},
		// 内部方法
		_buttonTap() {
			if (this.playing) this.pause();
			else this.play();
		},
		_seeking(e) {
			if (e.timeStamp - this.lastTime < 200) return;
			var time = Math.round(e.detail.value / 100 * this._ctx.duration),
				min = parseInt(time / 60),
				sec = time % 60;
			this.time = (min > 9 ? min : '0' + min) + ':' + (sec > 9 ? sec : '0' + sec);
			this.lastTime = e.timeStamp;
		},
		_seeked(e) {
			this.seek(e.detail.value / 100 * this._ctx.duration);
			this.lastTime = void 0;
		}
	}
}
